<template>
  <div class="container">
    <h1>{{ uniqueId }}</h1>
    <ul id="messages" class="messages-container">
      <transition-group name="list" enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
        <li v-for="(message, index) in messages" :key="index"
            v-bind:class="{ 'message-container': (message.from !== uniqueId), 'own-message-container': (message.from === uniqueId)}">
          <div class="message">
            <span>{{ message.text }}</span>
          </div>
        </li>
      </transition-group>
    </ul>
    <div class="input-container">
      <form @submit.prevent="sendMessage" class="message-input">
        <input type="text" placeholder="Enter your message" v-model="message">
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Chat",
  data() {
    return {
      uniqueId: false,
      message: "",
      messages: []
    };
  },

  methods: {
    sendMessage() {
      this.$socket.emit("new_message", { message: this.message, from: this.uniqueId });
      this.message = "";
    }
  },

  sockets: {
    connect(data) {
      this.isConnected = true;
    },

    disconnect() {
      this.isConnected = false;
    },

    connected_user(data) {
      if (!this.uniqueId) {
        this.uniqueId = data.uniqueId;
      }
    },

    new_message(data) {
      console.log(data);
      this.messages.push(data);
      setTimeout(() => {
      var container = this.$el.querySelector("#messages");
      container.scrollTop = container.scrollHeight + 1000;
      }, 20);
    }
  }
};
</script>

